<template>
  <div class="tabs">
    <router-link
      :to="{ path: path.path }"
      v-for="path in paths"
      :key="path.id">{{ path.title }}</router-link>
  </div>
</template>

<script>
  export default {
    name: 'tabs',
    props: ['paths']
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .tabs{
    text-align: center;
    display: inline-block;
    position: absolute;
    left: 50%;
    top: 57px;
    transform: translate(-50%);
    border: 1px solid #00ca79;
    border-radius: 0 0 10px 10px;
    font-size: 0px;
  }
  .tabs a{
    display: inline-block;
    width: 88px;
    height: 30px;
    line-height: 30px;
    font-size: 12px;
    text-align: center;
  }
  .tabs a:first-child{
    border-radius: 0 0 0 9px;
  }
  .tabs a:nth-of-type(2){
    border-right: 1px solid #00ca79;
    border-left: 1px solid #00ca79;
  }
  .tabs a:last-child{
    border-radius: 0 0 9px 0;
  }
  .tabs .router-link-active{
    background-color: #00ca79;
    color: #fff;
    transition: .5s;
    font-weight: bold;
  }
</style>



